<template>
  <div>
    <transition name="fade">
      <div class="modal is-active" v-if="show">
        <div class="modal-background" @click="$emit('close')"></div>
        <div class="modal-content fd-modal-card">
          <div class="card">
            <div class="card-content">
              <p class="title is-4">
                {{ track.title }}
              </p>
              <p class="subtitle">
                {{ track.artist }}
              </p>
              <div class="buttons" v-if="track.media_kind === 'podcast'">
                <a class="button is-small" v-if="track.play_count > 0" @click="mark_new">Mark as new</a>
                <a class="button is-small" v-if="track.play_count === 0" @click="mark_played">Mark as played</a>
              </div>
              <div class="content is-small">
                <p>
                  <span class="heading">Album</span>
                  <a class="title is-6 has-text-link" @click="open_album">{{ track.album }}</a>
                </p>
                <p v-if="track.album_artist && track.media_kind !== 'audiobook'">
                  <span class="heading">Album artist</span>
                  <a class="title is-6 has-text-link" @click="open_artist">{{ track.album_artist }}</a>
                </p>
                <p v-if="track.composer">
                  <span class="heading">Composer</span>
                  <span class="title is-6">{{ track.composer }}</span>
                </p>
                <p v-if="track.date_released">
                  <span class="heading">Release date</span>
                  <span class="title is-6">{{ track.date_released | time('L') }}</span>
                </p>
                <p v-else-if="track.year > 0">
                  <span class="heading">Year</span>
                  <span class="title is-6">{{ track.year }}</span>
                </p>
                <p v-if="track.genre">
                  <span class="heading">Genre</span>
                  <a class="title is-6 has-text-link" @click="open_genre">{{ track.genre }}</a>
                </p>
                <p>
                  <span class="heading">Track / Disc</span>
                  <span class="title is-6">{{ track.track_number }} / {{ track.disc_number }}</span>
                </p>
                <p>
                  <span class="heading">Length</span>
                  <span class="title is-6">{{ track.length_ms | duration }}</span>
                </p>
                <p>
                  <span class="heading">Path</span>
                  <span class="title is-6">{{ track.path }}</span>
                </p>
                <p>
                  <span class="heading">Type</span>
                  <span class="title is-6">{{ track.media_kind }} - {{ track.data_kind }} <span class="has-text-weight-normal" v-if="track.data_kind === 'spotify'">(<a @click="open_spotify_artist">artist</a>, <a @click="open_spotify_album">album</a>)</span></span>
                </p>
                <p>
                  <span class="heading">Quality</span>
                  <span class="title is-6">
                    {{ track.type }}
                    <span v-if="track.samplerate"> | {{ track.samplerate }} Hz</span>
                    <span v-if="track.channels"> | {{ track.channels | channels }}</span>
                    <span v-if="track.bitrate"> | {{ track.bitrate }} Kb/s</span>
                  </span>
                </p>
                <p>
                  <span class="heading">Added at</span>
                  <span class="title is-6">{{ track.time_added | time('lll') }}</span>
                </p>
                <p>
                  <span class="heading">Rating</span>
                  <span class="heading"><star-rating v-model="rating"
                    :star-size="24"
                    :padding="7"
                    :show-rating="false"
                    :max-rating="5"
                    :increment="1"
                    :inline="true"
                    :clearable="true"
                    :active-on-click="true"
                    @rating-selected="rate_track"></star-rating> </span>
                  <span class="title is-6">{{ Math.floor(track.rating / 10) }} / 10</span>
                </p>
                <div v-if="this.track.data_kind === 'file'">
                  <p>
                    <span class="heading">Usermark</span>
                  </p>
                  <div class="buttons">
                    <a :disabled="this.usermark_is_set(1)" class="button is-small is-danger" @click="usermark_update(1)">Mark to delete</a>
                    <a :disabled="this.usermark_is_set(2)" class="button is-small is-warning" @click="usermark_update(2)">Mark to rexcode</a>
                    <a :disabled="this.usermark_is_set(4)" class="button is-small is-warning" @click="usermark_update(4)">Mark to review</a>
                    <a :disabled="this.usermark === 0" class="button is-small is-success" @click="usermark_update(0)">Mark reset</a>
                  </div>
                </div>
                <p v-if="track.comment">
                  <span class="heading">Comment</span>
                  <span class="title is-6">{{ track.comment }}</span>
                </p>
              </div>
            </div>
            <footer class="card-footer">
              <a class="card-footer-item has-text-dark" @click="queue_add">
                <span class="icon"><i class="mdi mdi-playlist-plus"></i></span> <span class="is-size-7">Add</span>
              </a>
              <a class="card-footer-item has-text-dark" @click="queue_add_next">
                <span class="icon"><i class="mdi mdi-playlist-play"></i></span> <span class="is-size-7">Add Next</span>
              </a>
              <a class="card-footer-item has-text-dark" @click="play_track">
                <span class="icon"><i class="mdi mdi-play"></i></span> <span class="is-size-7">Play</span>
              </a>
            </footer>
          </div>
        </div>
        <button class="modal-close is-large" aria-label="close" @click="$emit('close')"></button>
      </div>
    </transition>
  </div>
</template>

<script>
import webapi from '@/webapi'
import SpotifyWebApi from 'spotify-web-api-js'
import StarRating from 'vue-star-rating'

export default {
  name: 'ModalDialogTrack',
  components: { StarRating },

  props: ['show', 'track'],

  data () {
    return {
      rating: 0,
      usermark: -1,
      spotify_track: {}
    }
  },

  methods: {
    play_track: function () {
      this.$emit('close')
      webapi.player_play_uri(this.track.uri, false)
    },

    rate_track: function (rating) {
      if (rating === 0.5) {
        rating = 0
      }
      this.rating = Math.ceil(rating) * 20
      webapi.library_track_set_rating(this.track.id, this.rating).then(() => {
        this.$emit('rating-updated', { track_id: this.track.id, rating: this.rating })
      })
    },

    queue_add: function () {
      webapi.queue_add(this.track.uri).then(() => {
        this.$emit('close')
      })
    },

    queue_add_next: function () {
      webapi.queue_add_next(this.track.uri).then(() => {
        this.$emit('close')
      })
    },

    open_album: function () {
      this.$emit('close')
      if (this.track.media_kind === 'podcast') {
        this.$router.push({ path: '/podcasts/' + this.track.album_id })
      } else if (this.track.media_kind === 'audiobook') {
        this.$router.push({ path: '/audiobooks/' + this.track.album_id })
      } else {
        this.$router.push({ path: '/music/albums/' + this.track.album_id })
      }
    },

    open_artist: function () {
      this.$emit('close')
      this.$router.push({ path: '/music/artists/' + this.track.album_artist_id })
    },

    open_genre: function () {
      this.$router.push({ name: 'Genre', params: { genre: this.track.genre } })
    },

    open_spotify_artist: function () {
      this.$emit('close')
      this.$router.push({ path: '/music/spotify/artists/' + this.spotify_track.artists[0].id })
    },

    open_spotify_album: function () {
      this.$emit('close')
      this.$router.push({ path: '/music/spotify/albums/' + this.spotify_track.album.id })
    },

    usermark_is_set: function (value) {
      return (this.usermark & value) > 0
    },

    usermark_update (value) {
      const newvalue = value === 0 ? 0 : value | this.usermark
      webapi.library_track_set_usermark(this.track.id, newvalue).then(() => {
        this.usermark = newvalue
        this.$emit('usermark-updated', { value: this.usermark, track_id: this.track.id })
      })
    },

    mark_new: function () {
      webapi.library_track_update(this.track.id, { play_count: 'reset' }).then(() => {
        this.$emit('play-count-changed')
        this.$emit('close')
      })
    },

    mark_played: function () {
      webapi.library_track_update(this.track.id, { play_count: 'increment' }).then(() => {
        this.$emit('play-count-changed')
        this.$emit('close')
      })
    }
  },

  watch: {
    'track' () {
      if (this.track && this.track.data_kind === 'spotify') {
        const spotifyApi = new SpotifyWebApi()
        spotifyApi.setAccessToken(this.$store.state.spotify.webapi_token)
        spotifyApi.getTrack(this.track.path.slice(this.track.path.lastIndexOf(':') + 1)).then((response) => {
          this.spotify_track = response
        })
      } else {
        this.spotify_track = {}
        this.rating = this.track.rating
        if (this.track.data_kind === 'file') {
          webapi.library_track(this.track.id).then((response) => {
            this.usermark = response.data.usermark
          }).catch(() => {
            this.usermark = -1
          })
        }
      }
    }
  }
}
</script>

